'use client';
import React, { useState } from 'react';
import { Button, Flex, Typography, Space, Grid } from 'antd';
import { FireFilled } from '@ant-design/icons';
import { getCardStack, isProfile } from '@/utils/mockData';
import { SwipeCard } from '../../../components/swipe/SwipeCard';
import DashboardLayout from '@/components/DashboardLayout';

const { Title, Text } = Typography;
const { useBreakpoint } = Grid;

const Swipe: React.FC = () => {
  const [cards, setCards] = useState(getCardStack());
  const [triggerSwipe, setTriggerSwipe] = useState<'left' | 'right' | null>(null);
  const [swipeHistory, setSwipeHistory] = useState<any[]>([]);
  const screens = useBreakpoint();

  const handleSwipe = (direction: 'left' | 'right') => {
    setCards((prev) => prev.slice(0, -1));
  };

  const handleUndo = () => {
    const lastSwipe = swipeHistory.pop();
    if (lastSwipe) {
      setCards((prev) => [...prev, lastSwipe.card]);
      setSwipeHistory([...swipeHistory]);
    }
  };

  const handleReject = () => setTriggerSwipe('left');
  const handleLike = () => setTriggerSwipe('right');
  const handleSuperLike = () => setTriggerSwipe('right');
  const handleBoost = () => console.log('Boost activated!');

  return (
    <DashboardLayout>
      <div
        style={{
          height: '100dvh',
          background: '#0a0a12',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          position: 'relative',
          paddingBottom: screens.xs ? '80px' : '100px',
        }}
      >
        {/* Swipe Area */}
        <div
          style={{
            flex: 1,
            width: '100%',
            maxWidth: screens.md ? 500 : '100%',
            padding: screens.xs ? '0.5rem' : '1rem',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            overflow: 'hidden',
            position: 'relative',
          }}
        >
          {cards.length === 0 ? (
            <Space direction="vertical" align="center" style={{ padding: 40 }}>
              <Flex
                align="center"
                justify="center"
                style={{
                  width: 80,
                  height: 80,
                  borderRadius: '50%',
                  background:
                    'linear-gradient(to right, rgba(236,72,153,0.2), rgba(239,68,68,0.2))',
                }}
              >
                <FireFilled style={{ fontSize: 36, color: '#ec4899' }} />
              </Flex>
              <Title level={4} style={{ color: 'white', marginBottom: 4, textAlign: 'center' }}>
                You're all caught up!
              </Title>
              <Text style={{ color: '#aaa', textAlign: 'center' }}>
                Check back later for new profiles
              </Text>
              <Button
                type="primary"
                shape="round"
                style={{
                  background: 'linear-gradient(to right, #ec4899, #ef4444)',
                  border: 'none',
                  marginTop: 12,
                }}
                onClick={() => setCards(getCardStack())}
              >
                Refresh
              </Button>
            </Space>
          ) : (
            <div
              style={{
                position: 'relative',
                width: '100%',
                height: screens.xs ? '70vh' : 'calc(100vh - 220px)',
                maxHeight: '800px',
                margin: '0 auto',
              }}
            >
              {cards.slice(-3).reverse().map((card, index) => {
                const isTopCard = index === 0;
                const cardStyle = {
                  position: 'absolute' as const,
                  top: 0,
                  left: 0,
                  width: '100%',
                  height: '100%',
                  transform: `scale(${1 - index * 0.04}) translateY(${index * -8}px)`,
                  zIndex: 50 - index,
                  opacity: index === 0 ? 1 : 0.8,
                };

                if (isProfile(card)) {
                  return (
                    <SwipeCard
                      key={card.id}
                      profile={card}
                      onSwipe={handleSwipe}
                      style={cardStyle}
                      triggerSwipe={isTopCard ? triggerSwipe : null}
                      onSwipeComplete={() => setTriggerSwipe(null)}
                    />
                  );
                } else {
                  return (
                    <AdCard
                      key={card.id}
                      ad={card}
                      onSwipe={handleSwipe}
                      style={cardStyle}
                      triggerSwipe={isTopCard ? triggerSwipe : null}
                      onSwipeComplete={() => setTriggerSwipe(null)}
                    />
                  );
                }
              })}
            </div>
          )}
        </div>

        {/* Action Buttons (Fixed Bottom) */}
        {cards.length > 0 && (
          <div
            style={{
              position: 'fixed',
              bottom: screens.xs ? 16 : 32,
              left: '50%',
              transform: 'translateX(-50%)',
              zIndex: 100,
              width: screens.xs ? '90%' : 'auto',
              display: 'flex',
              justifyContent: 'center',
            }}
          >
            <SwipeActionButtons
              onUndo={handleUndo}
              onReject={handleReject}
              onSuperLike={handleSuperLike}
              onLike={handleLike}
              onBoost={handleBoost}
              canUndo={swipeHistory.length > 0}
            />
          </div>
        )}
      </div>
    </DashboardLayout>
  );
};

export default Swipe;
